import React from "react";
import {pobierzDaneGminy} from "./dane/actions";
import {connect, ConnectedProps} from "react-redux";
import {RootState} from "./reducers";
import {changeKodGminy} from "./konfiguracja/actions";

class SelektorGminyInternal extends React.Component<PropsFromRedux> {
    render() {
        const {pobierzDaneGminy, lista_powiatow, changeKodGminy, kod_gminy} = this.props;
        return lista_powiatow?<select id="selektor-gminy"
                                      value={kod_gminy}
                               onChange={ev => {
                                   changeKodGminy(ev.target.value);
                                   pobierzDaneGminy(ev.target.value);
                               }}>
            {Array.from(lista_powiatow.values(), (value) => (
                    <optgroup key={value.powiat} label={value.powiat}>
                        {Array.from(value.gminy.values(), (gmina) => (
                                <option key={gmina.gmina} value={gmina.kod_gminy}>
                                    {gmina.gmina}
                                </option>
                            ))}
                    </optgroup>
            ))}
        </select>:<React.Fragment />;
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        lista_powiatow: state.lista_powiatow,
        kod_gminy: state.kod_gminy,
    };
};

const mapDispatchToProps = {
    pobierzDaneGminy,
    changeKodGminy,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(SelektorGminyInternal);